
#app {
  font-family: 'Open Sans', sans-serif;
  font-size: 13px;
}
.aes-button {
  background: transparent;
  border-radius: 20px;
  padding: 5px 15px;
}
.aes-button:hover {
  text-decoration: none;
}
.aes-button--empty {
  border: 1px solid rgba(255, 255, 255, 0.3);
}
.aes-button--empty:hover {
  border-color: rgba(255, 255, 255, 0.9);
}
.footer {
  background-color: #161616;
  height: 200px;
  color: #fff;
}



