
.jumbo {
  background: #eee;
  height: 800px;
}
.jumbo .container {
  padding: 150px 30px;
}
.jumbo__header {
  font-size: 80px;
  line-height: 80px;
  font-weight: 700;
}
.jumbo__header--condensed {
  width: 700px;
}
.jumbo__description {
  font-size: 28px;
}
.jumbo__announcement {
  width: 700px;
  margin-top: 40px;
  line-height: 26px;
  padding: 3px 12px 3px 5px;
  border-radius: 15px;
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  background: rgba(0, 0, 161, 0.05);
  font-size: 12px;
  z-index: 20;
}
.jumbo__announcement .new-pill {
  color: #fff;
  margin-left: 10px;
  padding: 0 6px;
  font-size: 10px;
  line-height: 16px;
  font-weight: 600;
  background: #24b47e;
  text-transform: uppercase;
  border-radius: 10px;
}
.jumbo__announcement .message {
  margin: 0 7px 0 6px;
}
.jumbo__announcement:hover {
  text-decoration: none;
  background: rgba(255, 255, 255, 0.1);
}
.jumbo--inverse {
  background-color: #161616;
  color: #fff;
}



